import type { CompilerOptions } from 'typescript';

interface ConfigOptions {
  compilerOptions?: CompilerOptions;
  exclude?: string[];
  extends?: string;
  files?: string[];
  include?: string[];
  references?: string[];
}

const BASE_CONFIG = {
  compilerOptions: {
    allowJs: true,
    baseUrl: 'src',
    declaration: false,
    esModuleInterop: true,
    isolatedModules: true,
    lib: ['ESNext'],
    module: 'NodeNext',
    moduleDetection: 'force',
    moduleResolution: 'NodeNext',
    noFallthroughCasesInSwitch: true,
    noImplicitAny: true,
    noImplicitOverride: true,
    noUncheckedIndexedAccess: true,
    resolveJsonModule: true,
    skipLibCheck: true,
    sourceMap: true,
    strict: true,
    strictNullChecks: true,
    inlineSources: true,
    target: 'ES2015',
    verbatimModuleSyntax: true,
    types: ['node'],
  },
  exclude: ['node_modules'],
} as const;

type MergeOptions<Options extends ConfigOptions> = typeof BASE_CONFIG &
  Options & {
    compilerOptions: typeof BASE_CONFIG.compilerOptions &
      Options['compilerOptions'];
  };

export function createConfig<const Options extends ConfigOptions>(
  options: Options,
): MergeOptions<Options> {
  return {
    ...BASE_CONFIG,
    ...options,
    compilerOptions: {
      ...BASE_CONFIG.compilerOptions,
      ...options.compilerOptions,
    },
  };
}
